<template>
  <div>
    <div align="center">
      <v-avatar :size="sizer()">
        <v-img src="@/assets/bakiiun.jpg" />
      </v-avatar>
      <v-flex id="Hello" class="display-3 mt-7">
        <v-icon size="40" class="mb-2 mr-1" color="light-blue darken-2">mdi-pound</v-icon>Hello
      </v-flex>
      <v-card class="body-1 mt-5" max-width="800" flat color="transparent">{{about}}</v-card>
      <v-flex id="Projects" class="display-3 mt-7 mb-5">
        <v-icon size="40" class="mb-2 mr-1" color="light-blue darken-2">mdi-pound</v-icon>Projects
      </v-flex>
    </div>
    <v-row class="d-flex justify-space-around">
      <v-col cols="12" md="4" v-for="project in pList" :key="project.id">
        <v-card width="350" tile>
          <v-card class="pa-3" flat tile>
            <v-card flat tile>
              <v-img height="200px" :src="logo(project.logo)" class="mb-3" />
              <v-btn
                small
                fab
                bottom
                right
                color="grey darken-3"
                target="_blank"
                absolute
                v-bind:href="project.link"
              >
                <v-icon size="20" color="white">mdi-launch</v-icon>
              </v-btn>
            </v-card>
            <v-chip small pill v-bind:color="project.color" text-color="white" class="mb-1">
              <v-icon class="mr-1" color="white" small>{{project.tagicon}}</v-icon>
              {{project.tag}}
            </v-chip>
            <div class="mb-1 title">{{project.name}}</div>
            <div class="body-2">{{project.description}}</div>
            <div class="mt-3 mb-1 title">Used Tech</div>
            <v-row>
              <div v-for="tech in project.techs" :key="tech.id">
                <v-card width="80" height="80" class="text-center" flat>
                  <v-avatar>
                    <v-icon>{{tech.icon}}</v-icon>
                  </v-avatar>
                  <div class="caption">{{tech.name}}</div>
                </v-card>
              </div>
            </v-row>
          </v-card>
        </v-card>
      </v-col>
    </v-row>
    <div id="Contact" class="display-3 mt-7 mb-5" align="center">
      <v-icon size="40" class="mb-2 mr-1" color="light-blue darken-2">mdi-pound</v-icon>Contact
    </div>
    <v-row>
      <v-col cols="12" md="4" v-for="contact in cList" :key="contact.id">
        <v-hover v-slot:default="{ hover }">
          <v-card
            width="350"
            :elevation="hover ? 16 : 2"
            shaped
            target="_blank"
            v-bind:href="contact.link"
          >
            <v-container>
              <v-flex class="d-flex">
                <v-avatar class="my-auto" icon>
                  <v-icon large>{{contact.icon}}</v-icon>
                </v-avatar>
                <v-flex class="my-auto">
                  <v-card-title>{{contact.title}}</v-card-title>
                  <v-card-subtitle>{{contact.subtitle}}</v-card-subtitle>
                </v-flex>
              </v-flex>
            </v-container>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
    <v-footer color="transparent">
      <div class="mx-auto">
        {{ new Date().getFullYear() }} — ©
        <strong>bakiiun</strong>
      </div>
    </v-footer>
  </div>
</template>

<script>
export default {
  props: {
    about: {
      default: function() {
        return {};
      }
    },
    pList: {
      default: function() {
        return {};
      }
    },
    cList: {
      default: function() {
        return {};
      }
    }
  },

  methods: {
    sizer() {
      return window.screen.width <= 600 ? "200" : "400";
    },
    logo(logo) {
      return require("../assets/" + logo);
    }
  }
};
</script>

<style>
</style>